
/*== Sticky header ==*/
.header {
    position: relative;
    z-index: 5;
    padding: 0px;
    background: transparent;
    @include md-screen {
        top: 0px;
        padding: 18px 36px;
        position: absolute;
        left: 0;
        right: 0;
        z-index: 7;
        max-height: 101vh;
        overflow-y: auto;
        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 0px;
        }
    }
    @include msm-screen{
        padding: 12px 0px;
    }
    &.fixed-header {
        background-color: hsl(var(--white));
        position: fixed;
        top: 0px;
        animation: slide-down 0.8s;
        width: 100%;
        box-shadow: rgb(9 9 9/ 5%) 0px 1px 0px;
        border-bottom: 1px solid hsl(var(--white)/.08);
        padding: 0px;
        z-index: 99;

        .header-wrapper{
            padding: 22px 0px;
        }
        .header-wrapper .menu-wrapper ul.main-menu li a {
            color: hsl(var(--black));
        }
    }
  
}

.header-main-area {  
    .container-fluid {
        padding: 0 100px;
        @include xl-screen {
            padding: 0 60px;
        }
        @include msm-screen {
            padding: 0 25px;
        }
    }
}

.header-two{
    background: linear-gradient(120deg, hsl(var(--base)/0.3) 0%, hsl(var(--base-two)/0.2) 100%);
    .header-wrapper{
        padding: 22px 0px;
    }

    .header-wrapper .menu-wrapper ul.main-menu a.active{
        color:hsl(var(--black))
    }
    .header-wrapper .menu-wrapper ul.main-menu a {
        color: hsl(var(--black));
    }
    .language-box .select{
        color: hsl(var(--black));
    }
    .language-box i{
        color: hsl(var(--dark));
    }
    .header.fixed-header{
        background: hsl(var(--white));
    }
    .header.fixed-header .header-wrapper .menu-wrapper ul.main-menu a {
        color: hsl(var(--black));
    }
    .header-wrapper .ham__menu{
        color: hsl(var(--base));
    }
}


.scrolled{
    display: none !important;
}
.header-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 34px 0px;
    @include md-screen {
        padding: 0px 0;
    }
    @include msm-screen {
        padding: 0px 15px !important;
    }
    .ham__menu {
        color: hsl(var(--base));
        display: none ;
        @include md-screen{
            display: block !important;
        }
    }
    .logo-wrapper {
        img {
            max-width: 200px;
            filter: brightness(1);
            @include md-screen {
                width: 160px;
            }
            @include xsm-screen {
                width: 112px;
            }
        }
    }
    .menu-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        @include md-screen {
            display: none;
        }
        @include lg-screen {
            margin-left: 0px;
        }
       
        ul.main-menu {
            display: flex;
            li {
                    transition: all .4s;
                    
                }
                a {
                    font-size: 16px;
                    color: hsl(var(--white));
                    font-weight: 500;
                    position: relative;
                    text-transform: uppercase;
                    margin-right: 22px;
                    &:before {
                        position: absolute;
                        bottom: -6px;
                        content: '';
                        width: 100%;
                        height: 3px;
                        background: hsl(var(--base));
                        transform: translateX(-100%);
                        opacity: 0;
                      }

                    &.active {
                        color: hsl(var(--white));
                        
                            &:before {
                              opacity: 1;
                              transform: translateX(0);
                            }
                          
                    }
                    @include lg-screen {
                        padding: 0px 12px;
                    }
                    &:hover {
                        &:before {
                        opacity: 1;
                        transition: 0.5s transform ease, 0.8s opacity ease;
                        transform: translateX(0);
                      }
                    }
                }
        }

        
    }


    // search box

    .search-box-wrap{
        width: 648px;
        form{
            width: 100%;
            display: flex;
            .header-input{
                background: var(--header-bg);
                border-radius: 4px 0px 0px 4px;
                padding: 12px 10px;
                border: 1px solid hsl(var(--white)/0.1);
                color: hsl(var(--white)) !important;
                &::placeholder{
                    color: hsl(var(--white)/.6) !important;
                }
            }
            .header-select{
                background: var(--header-bg);
                border-radius: 0px;
                border: 1px solid hsl(var(--white)/0.1);
                color: hsl(var(--white)/0.6) !important;
                option {
                    background: hsl(var(--base));
                    color: hsl(var(--black));
                    &:hover{
                        background-color: hsl(var(--base)/.5) !important;
                    }
                }
            }
            .header-sub-btn{
                padding: 0 14px;
                border-radius: 0px 4px 4px 0px;
                i{
                    font-size: 23px;
                }
            }
        }
    }

    .menu-right-wrapper {
        display: flex;
        ul {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 4px;
            gap: 12px;
            li {
                margin-left: 12px;
                a{
                    color: hsl(var(--white));
                }
                .cart-btn{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    background: hsl(var(--white)/.08);
                    i{
                        color: hsl(var(--white));
                        font-size: 23px;
                        margin: 0px !important;
                    }
                }
                @include xsm-screen {
                    margin-left: 15px;
                }
                &:first-child {
                    margin-left: 0px;
                }
                &:nth-child(3) {
                    @include md-screen {
                        display: block;
                        visibility: visible;
                    }
                }
                .sidebar-menu-show-hide {
                    background: hsl(var(--base));
                    padding: 9px 12px;
                    color: hsl(var(--white));
                    border-radius: 3px;
                }

               @include md-screen {
                a i {
                    margin-top: 8px;
                }
               }
            }                               
        }
    }


    @media (max-width: 770px) {
        
        .menu-right-wrapper{
          display: block !important;
          ul{
            gap: 0px;
          }
        }
      }


      @media (max-width: 620px) {
        
        .menu-right-wrapper{
          
          ul:last-child{
            display: none;
          }
        }
      }


    .count-item {
        position: absolute;
        height: 19px;
        width: 19px;
        background: hsl(var(--base));
        text-align: center;
        border-radius: 50%;
        font-size: 13px;
        color: hsl(var(--white));
        top: -7px;
        right: 7px;
        font-weight: 600;
        line-height: 19px;
        @include md-screen {
            top: -4px;
        }
    }
}

