// mobile menu search box



.mobile-search-box{
    display: none;
    .mobile-search-wrap{
        display: flex;
        gap: 12px;
    }
    .input-show{
        display: block !important;
    }
    .input-wrap{
        display: none;
    }
    .mobile-search-input{
        border: 1px solid hsl(var(--white));
        padding: 8px 10px;
        width: 340px;
        color: hsl(var(--white)) !important;
        &:focus{
            border: 1px solid hsl(var(--base)/0.5);
        }
        &::placeholder{
            color: hsl(var(--white)) !important;
        }
    }
    button{
        i{
            color: hsl(var(--base));
            font-size: 22px;
        }
    }
}

// dropdown menu
.profile-option{
    position: relative;
    &:hover{
        .profile-dropdown-menu{
            display: block;
        }
    }
}
.profile-dropdown-menu{
    display: none;
    position: absolute;
    right: 0;
    ul{
        background: hsl(var(--white));
        display: flex;
        flex-direction: column;
        width: 200px;
        border-radius: 4px;
        justify-content: left !important;
        align-items: flex-start !important;
        padding: 12px;
        li{
            margin: 0px 0px 8px 0px!important;
           
            &:last-child{
                margin: 0px !important;
            }
            a{
                color: hsl(var(--black)) !important;
                display: flex !important;
                position: relative;
                padding-bottom: 6px;
                border-bottom: 1px solid hsl(var(--base)/.3);
                &:hover{
                    margin-left: 4px;
                }
            }
        }
    }
}

.menu-search-wrap {
    position: relative;
    width: 10px;
    .menu-search-wrap--search-icon {
        cursor: pointer;
        i {
            font-size: 17px;
            color: hsl(var(--black));;
        }
    }
    .header-search-hide-show {
        display: none;
        transition: all .4s ease-in-out;
        &.show {
            display: block;
            right: -18px;
            transition: all .4s ease-in-out;
        }
    }
    .close-hide-show {
        display: none;
        &.show{
            display: block;
            position: relative;
            z-index: 2;
        }
    }
}

.search-box.header-search {
    width: 260px;
    position: absolute;
    right: -7px;
    top: 35px;
    z-index: 2;
    transition: all 0.4s ease-in-out;
    @include xsm-screen {
        width: 170px;
    }
    input.form--control {
        padding-right: 52px;
        border: 1px solid hsl(var(--base)/.2);
        &::placeholder {
            color: hsl(var(--dark));
        }
    }

    button.search-box__button {
        top: 27px;
        right: 0;
        background: hsl(var(--base));
        height: 100%;
        color: hsl(var(--dark));
        padding: 14px;
        border-radius: 0 10px 10px 0px;
        border: 1px solid hsl(var(--white)/.5);
        i {
            color: hsl(var(--white));
        }
    }
    .form--control {
        background: hsl(var(--base));
    }
}
.menu-search-wrap .menu-search-wrap--search-icon i {
    font-size: 17px;
    color: hsl(var(--black));
    margin-top: 4px;
    position: absolute;
    margin-left: 13px;
}

/*========== Sticky Menu Css ========*/

.header.fixed-header {
    .header-wrapper .menu-wrapper ul.main-menu a {
        color: hsl(var(--white));
        &:hover {
            color: hsl(var(--base));
        }
        &.active {
            color: hsl(var(--base));            
        }
    }
    .light-dark-btn-wrap .mon-icon {
        color: hsl(var(--black));
    }
    .menu-search-wrap .menu-search-wrap--search-icon i {
        font-size: 17px;
        color: hsl(var(--black));
    }
    // .header-wrapper .ham__menu{
    //     color: hsl(var(--black));
    // }
}

// language dropdown
.language{
    position: relative;
}
.is-open-language-dropdown{
    .language-dropdown{
        display: block;
    }
}
.language-dropdown{
    position: absolute;
    top: 28px;
    right: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    display: none;
    .language-dropdown-menu{
        width: 92px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: hsl(var(--base));
        padding: 4px 0px;
        gap: 8px;
        border-radius: 4px;
    }
    .language-dropdown-list{
        margin: 0px !important;
        padding: 0px 6px;
        &:hover{
            background-color: hsl(var(--white));
            transition: 0.2s ease-in-out;
            border-radius: 4px;
        }
    }
}




.home{
    &:hover{
        transition: 2s linear;
        .dropdown-homemenu{
            display: block;
            opacity: 1;
            visibility: visible;
        }
    }
    }
    
.dropdown-homemenu {
    position: absolute;
    top: 68px;
    padding: 15px 0px;
    transition: 2s linear;
    background-color: hsl(var(--white));
    border: 1px solid hsl(var(--base));
    border-radius: 6px;
    box-shadow: 0 0 20px 0 rgba(14,41,50,.17);
    min-width: 178px;
    opacity: 0;
    transition-duration: .2s;
    visibility: hidden;
    a {
        margin: 0 !important;
        font-size: 14px !important;
    }
}


    



// Responsive code
@media (max-width:1199px){
    .header-wrapper .menu-wrapper ul.main-menu a {
        margin: 0px;
    }
    .header-wrapper .menu-right-wrapper ul {
        gap: 0px;
    }
    .header-wrapper .search-box-wrap{
        width: 500px;
    }
}
@media (max-width:992px){
    .header-wrapper .search-box-wrap{
        display: none;
    }
    .header-wrapper-2{
        display: none;
    }
    .scrolled{
        display: flex !important;
    }
}
@media (max-width: 769px) {
    .ham__menu {
      display: block !important;
    }
    // .nav-link{
    //   display: none !important;
    // }
    .header-wrapper .menu-right-wrapper ul li .notification-dropdown {
        width: 312px;
        position: fixed;
        top: 48px;
        z-index: 9;
    }
    .header-wrapper .menu-right-wrapper ul li .message-dropdown {
        
        position: fixed;
        top: 48px;
        width: 312px;
        z-index: 9;
    }
    .language-dropdown {
        width: 90px;
        position: fixed;
        top: 44px;
        right: 74px !important;

    }
}
@media (max-width:620px){
    .mobile-search-box{
        display: block;
    }
    
.input-show{
    display: none !important;
}

}
@media (max-width:450px){
    .mobile-search-input{
        width: 100% !important;
    }
}
@media (max-width: 426px) {

.header-menu-wrapper{
    margin-left: 14px;
}
.mobile-search-input{
    padding: 2px 10px !important; 
}
}
@media (max-width: 321px) {

.language-dropdown {
    right: 52px !important;
}
}
